.home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    position: relative;
    text-align: center;
    padding: 0 2em;
}

.home h1 {
    font-family: "Josefin Sans", sans-serif;
    font-size: 5vw;
    margin: 0;
    padding-bottom: 0.5rem;
    letter-spacing: 0.5rem;
    color: #03dac6;
    transition: all 0.3s ease;
    z-index: 3;
}

.home h1:hover {
    transform: translate3d(0, -22px, 22px);
    color: #ff0266;
}

.home p {
  padding: 5vw;
  max-width: 80vw;
}

.grid-content {
    display: grid;
    grid-gap: 1rem;
    padding: 2rem;
    max-width: 1280px;
    margin: 0 auto;
    font-family: 'Rubik', sans-serif;
  }
  
  .card {
    position: relative;
    display: flex;
    align-items: flex-end;
    overflow: hidden;
    padding: 1rem;
    width: 100%;
    text-align: center;
    color: rgb(var(--background-1));
    border-radius: 12px;
    background-color: rgb(var(--background-1));
    box-shadow: 0 1px 1px rgba(10, 10, 10, 0.1), 
      0 2px 2px rgba(10, 10, 10, 0.1), 
      0 4px 4px rgba(10, 10, 10, 0.1), 
      0 8px 8px rgba(10, 10, 10, 0.1), 
      0 16px 16px rgba(10, 10, 10, 0.1);
  }
  
  .card:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 110%;
    background-size: cover;
    background-position: 0 0;
    transition: transform 1.5s cubic-bezier(0.19, 1, 0.22, 1);
    pointer-events: none;
  }
    
  .card:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 200%;
    pointer-events: none;
    background-image: linear-gradient(to bottom, rgba(10, 10, 10, 0) 0%, 
      rgba(10, 10, 10, 0.009) 11.7%, rgba(10, 10, 10, 0.034) 22.1%, rgba(10, 10, 10, 0.072) 31.2%, 
      rgba(10, 10, 10, 0.123) 39.4%, rgba(10, 10, 10, 0.182) 46.6%, rgba(10, 10, 10, 0.249) 53.1%, 
      rgba(10, 10, 10, 0.32) 58.9%, rgba(10, 10, 10, 0.394) 64.3%, rgba(10, 10, 10, 0.468) 69.3%, 
      rgba(10, 10, 10, 0.54) 74.1%, rgba(10, 10, 10, 0.607) 78.8%, rgba(10, 10, 10, 0.668) 83.6%, 
      rgba(10, 10, 10, 0.721) 88.7%, rgba(10, 10, 10, 0.762) 94.1%, rgba(10, 10, 10, 0.79) 100%);
    transform: translateY(-50%);
    transition: transform 2s cubic-bezier(0.19, 1, 0.22, 1);
  }
  
  .content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 1rem;
    transition: transform 1s cubic-bezier(0.19, 1, 0.22, 1);
    z-index: 1;
  }
    
  .content > * + * {
    margin-top: 1rem;
  }
    
  .title {
    font-size: 1.3rem;
    font-weight: bold;
    line-height: 1.2;
    color: #f0f0f0;
  }
    
  .description {
    font-size: 1.125rem;
    font-style: italic;
    line-height: 1.35;
    color: #dcdcdc;
  }
  
  @media (hover: hover) and (min-width: 800px) {
    .card:after {
      transform: translateY(0);
    }
    
    .content {
      transform: translateY(calc(100% - 4.5rem));
    }
    
    .content > *:not(.title) {
      opacity: 0;
      transform: translateY(1rem);
      transition: transform 1s cubic-bezier(0.19, 1, 0.22, 1), opacity 1s cubic-bezier(0.19, 1, 0.22, 1);
    }
    
    .card:hover, .card:focus-within {
      align-items: center;
    }
  
    .card:hover:before, .card:focus-within:before {
      transform: translateY(-4%);
    }
  
    .card:hover:after, .card:focus-within:after {
      transform: translateY(-50%);
    }
  
    .card:hover .content, .card:focus-within .content {
      transform: translateY(0);
    }
  
    .card:hover .content > *:not(.title), .card:focus-within .content > *:not(.title) {
      opacity: 1;
      transform: translateY(0);
      transition-delay: 125ms;
    }
    
    .card:focus-within:before, .card:focus-within:after, .card:focus-within .content, .card:focus-within .content > *:not(.title) {
      transition-duration: 0s;
    }
  }
  
  @media (min-width: 800px) {
    .card:nth-child(11n + 1) {
      grid-column: span 1;
    }
    
    .card:nth-child(11n + 4) {
      grid-column: span 2;
      grid-row: span 1;
    }
    
    .card:nth-child(11n + 6) {
      grid-column: span 3;
      grid-row: span 1;
    }
    
    .card:nth-child(11n + 8) {
      grid-column: span 2;
      grid-row: span 2;
    }
    
    .card:nth-child(11n + 9) {
      grid-row: span 3;
    }
    
    .card:nth-child(11n + 10) {
      grid-column: span 2;
      grid-row: span 2;
    }
  }
  
  /* TODO: Assign to each child a background */
  
  .card:nth-child(n):before{
      background-image: url('/public/images/backgrounds/1.png');
  } 
  
  .card:nth-child(2):before{
    background-image: url('/public/images/backgrounds/2.png');
  } 
  
  .card:nth-child(3):before{
    background-image: url('/public/images/backgrounds/3.png');
  } 
  
  .card:nth-child(4):before{
    background-image: url('/public/images/backgrounds/4.png');
  } 
  
  .card:nth-child(5):before{
    background-image: url('/public/images/backgrounds/5.png');
  } 
  
  .card:nth-child(6):before{
    background-image: url('/public/images/projects/shinwa-no-kugun/background.png');
  } 